function InputUnit({ labeling, type, name, value, funcChange, invalidInputs }){
    const errorHere = invalidInputs.field.includes(name)
    return (
        <label>
            {labeling}{ errorHere?'**':''}
            <input type={type} value={value} name={name}
                onChange={funcChange} 
            />
        </label>
    )
}

export default InputUnit