import { useContext } from 'react'
import { useMutation } from '@apollo/react-hooks';
import { Redirect } from 'react-router-dom'
import gql from 'graphql-tag';

import { useFormUsageActions, useFormErrorActions, useLogRestriction 
} from '../utils/myHookFunctions'
import { localFormDataRevisions } from '../utils/dataRevision'
import { AuthContext } from '../utils/authorization';
import InputUnit from "../components/generalForm/InputUnit"
import ErrorUnit from '../components/generalForm/ErrorUnit'

function RegisterPage(){
    useLogRestriction('LOGOUT_NEEDED', '/') //at logged in state redirection

    const useFormRevisionCrit = ['email','username', 'password','passwordconf']

    const { values, onSubmit, onChange } = useFormUsageActions({
        email: '',
        username: '',
        password: '',
        passwordconf: ''
    }, userRegistration)

    const { errors, resetErrors, setValidationErrors, setGeneralErrors } = useFormErrorActions()
    const userContext = useContext(AuthContext)
    const [ createUser, { loading }] = useMutation(REGISTER_USER, {
        update( _, { data: { registration: userLogDatas } }){
            userContext.login(userLogDatas)
            resetErrors()
        }, 
        onError(err){
            setGeneralErrors(err.message)
        },
        variables: values
    })

    function userRegistration(){
        resetErrors()
        const clientErrors = localFormDataRevisions(values, useFormRevisionCrit)
        if(clientErrors.field.length === 0){
            createUser()
        }else{
            setValidationErrors(clientErrors)
        }
    }

    return (
        <div>
            <h3>Registration</h3>
            { userContext.user? <Redirect to='/' /> : <></>}
            <p>Please fill this form with some registration details.</p>
            <form>
                <InputUnit labeling='Email' 
                    type='email' invalidInputs={errors}
                    name='email' value={values.email} funcChange={onChange} />
                <InputUnit labeling='Username' 
                    type='text' invalidInputs={errors}
                    name='username' value={values.username} funcChange={onChange} />
                <InputUnit labeling='Password' 
                    type='password' invalidInputs={errors}
                    name='password' value={values.password} funcChange={onChange} />
                <InputUnit labeling='Confrim password' 
                    type='password' invalidInputs={errors}
                    name='passwordconf' value={values.passwordconf} funcChange={onChange} />
                <input type='submit' value='Send' onClick={onSubmit} />
                {   loading && <p>Under process...</p>  }
                {   errors.message.length > 0 && errors.message.map((errMsg, index)=>{
                        return <ErrorUnit key={index} text={errMsg}/>}) }
            </form>
        </div>
    )
}

const REGISTER_USER = gql`
    mutation registration(
        $email: String!,
        $username: String!,
        $password: String!,
        $passwordconf: String!
    ){
        registration(
            email: $email,
            username: $username,
            password: $password,
            passwordconf: $passwordconf
        ){
            id, email, username, token, tokenExpire, refreshToken, registeredAt, lastLoggedAt
        }
    }
`
/*
            friends{
                userid, username, email
            }, allPosts{
                postid, createdAt, updatedAt, content, owner{
                    userid, username, relation, mutualFriendCount
                }, sentiments{
                    sentimentid, createdAt, updatedA, content, owner{ 
                        userid, username, relation, mutualFriendCount }
                }, comments
            }, allChats{
                chatid, title, startedAt, owner{
                    userid, username, relation, mutualFriendCount
                }
            }
        
*/
export default RegisterPage