import React from "react";
import { useContext, useState } from 'react'
import { Link } from 'react-router-dom'

import { AuthContext } from '../utils/authorization'
import { usePathGetter } from '../utils/myHookFunctions'

function Navigation(){
    const userContext = useContext(AuthContext)
    const [ firstPathUnit ] = usePathGetter()
    const [ activeLink, setActiveLink ] = useState({  active: firstPathUnit.toLowerCase() })
    return (
        <nav>
            {userContext.user? <>
                <li>
                    <Link to='/account'>My account</Link>
                </li>
                <li>
                    <Link to='/friends'>Friends</Link>
                </li>
                <li>
                    <Link to='/posts'>Posts</Link>
                </li>
                <li>
                    <Link to='/chats'>Chatting</Link>
                </li>
                <li>
                    <Link to='/logout'>Logout</Link>
                </li>
            </> : <>
                <li>
                    <Link to='/login'>Login</Link>
                </li>
                <li>
                    <Link to='/registration'>Registration</Link>
                </li>
            </> }
        </nav>
    )
}

export default Navigation