
export const localFormDataRevisions = (values, criterias)=>{

    const errObj = { field: [], message: [] }
    for(const crit of criterias){

        const res = methodsForCriterias[crit](values)
        if(res){
            errObj.field.push(crit)
            errObj.message.push(res)
        }

    }
    return errObj
}

const unAnalyzer = new RegExp('[a-zA-Z0-9_ ]{4,}');
const pwdAnalyzer = new RegExp('.{6,}');
const emailAnalyzer = new RegExp('^[a-z0-9._]{3,}@[a-z]{3,}\.{1}[a-z]{2,3}$')

const methodsForCriterias = {
    email: (vls)=>{
        const parts = vls.email.split('.').reverse()
        if(parts[0].length < 2 || parts[0].length > 3 ){
            return 'Not acceptable email!';
        }
        if(!emailAnalyzer.test(vls.email)){
            return 'Not acceptable email!'
        }
        return ''
    },
    username: (vls)=>{
        if(vls.username.length > 50){
            return 'Too long username!'
        } 
        if(!unAnalyzer.test(vls.username)){
            return 'Not acceptable username!'
        }
        return ''
    },
    password: (vls)=>{
        if(!pwdAnalyzer.test(vls.password)){
            return 'Not acceptable password!'
        }
        return ''
    },
    passwordconf: (vls)=>{
        if(vls.password !== vls.passwordconf 
            || !pwdAnalyzer.test(vls.passwordconf)){
            return 'Not acceptable password confirmation!'
        }
        return ''
    }
}

function isItPassibleDBIndex(inputID){
    if(inputID.length !== 24){
        return true
    }
    const analyzer = new RegExp(/[a-f0-9]{24}/)
    return analyzer.test(inputID)
}

export const reviseAutTokenExpiry = (expireDateInMs)=>{
    try{
        const expireDate = new Date(expireDateInMs)
        const actDate = new Date()
        return expireDate >= actDate
    }catch(err){
        return true
    }
}

export const revisePasswordResetToken = (token)=>{

    const tokenParts = token.split('.')
    return isItPassibleDBIndex(tokenParts[0]) &&
        tokenParts[1] && tokenParts[2] && tokenParts[3]

}