import React, { useReducer, createContext, useEffect } from 'react';

import { useLazyQuery } from '@apollo/react-hooks'
import gql from 'graphql-tag';

import { didAuthTokenExpired,
    persistAllEssentialCredentials,
    clearAllLocalCredentials,
    didUserDatasPersistLocally    } from './localStorageProc'


/** __Initialization processes at page reload__ */
const activeUserSocket = null
const initAuthState = {
    user: null,
    newContent: [],
}


const FETCH_USER_DETAILS = gql`
    query requireClientContent{
        requireClientContent{
            id, email, username, registeredAt, lastLoggedAt
        }
    }
    `

const AuthContext = createContext({
    user: null,
    newContent: [],
    srvErrorMessage: '',
    login: (userData) => {},
    logout: () => {},
    srvError: (errorMsg) => {}
});


/** __Provider and reducer for published context__ */
function authReducer(state, action) {
    switch (action.type) {
        case 'LOGIN':
            return {  ...state, user: action.payload  };
        case 'LOGOUT':
            return { ...state, user: null, newContent: []  };
        case 'ERROR': 
            return { ...state, srvErrorMessage: action.payload}
        default:
            return state;
    }
}

function AuthorizationProvider(props){

    const [ authState, dispatch] = useReducer(authReducer, initAuthState);

    const [ refetchUserDatas, { called } ] = useLazyQuery(FETCH_USER_DETAILS, {
        onCompleted(data){
            dispatch({ type: 'LOGIN', payload: data.requireClientContent })
        },
        onError(err){
            console.log(err.message)
        }
    })

    useEffect(()=>{
        if(didUserDatasPersistLocally() && !authState.user && !called){
            refetchUserDatas()
        }
    })


    function login(userData) {
        //these will used at HttpLink at ApolloProvider
        persistAllEssentialCredentials(userData)
        delete userData.token
        delete userData.tokenExpire
        delete userData.refreshToken

        //these will user ad the app
        dispatch({ type: 'LOGIN',  payload: userData });
    }

    function logout() {
        clearAllLocalCredentials()
        dispatch({ type: 'LOGOUT' });
    }

    function srvError(errorMsg){
        dispatch({ type: 'ERROR', payload: errorMsg })
    }
    return (
        <AuthContext.Provider value={ {
                user: authState.user,
                newContent: authState.newContent,
                login, logout, srvError
            } }>
            {props.children}
        </AuthContext.Provider>
    )
}
export { AuthorizationProvider, AuthContext}
