import gql from 'graphql-tag'
import { useMutation } from '@apollo/client';
import { useParams, useHistory } from 'react-router';

import { 
    useFormUsageActions, useFormErrorActions, 
    useLogRestriction, useCookieErrorReport 
} from '../../utils/myHookFunctions'
import { revisePasswordResetToken } from '../../utils/dataRevision'
import { localFormDataRevisions } from '../../utils/dataRevision'

import InputUnit from '../generalForm/InputUnit'
import ErrorUnit from '../generalForm/ErrorUnit'
function PwdResetStage2(){

    useLogRestriction('LOGOUT_NEEDED', '/') //at logged in state redirection
    useCookieErrorReport('/error') //GET response server error case
    
    const routeHistory = useHistory()
    const routeParam = useParams()
    if(!revisePasswordResetToken(routeParam.token)){ 
        routeHistory.push('/') 
    }

    const userFormValidationCrit = ['password', 'passwordconf']
    const { 
        errors, resetErrors, setValidationErrors, setGeneralErrors 
    } = useFormErrorActions()
    const { 
        values, onSubmit, onChange, clearFields 
    } = useFormUsageActions(
        { password: '', passwordconf: '' }, userResetEmail)

    const [ resetStp1Process, { loading } ] = useMutation(RESET_PWD1, {
        update(_, { data: { resetPasswordStep3: accountProcess }} ){
            resetErrors()
            clearFields()
            routeHistory.replace('/passwordresetting/result')
        },
        onError(err){
            console.log(err)
            setGeneralErrors(err.message)
        },
        variables: values,
        context: {
            headers: { resetting: routeParam.token }
        }
    })

    function userResetEmail(){
        resetErrors()
        const clientErrors = localFormDataRevisions(values, userFormValidationCrit)
        if(clientErrors.field.length === 0){
            resetStp1Process()
        }else{
            setValidationErrors(clientErrors)
        }
    }

    return (
        <div>
            <h4>Forgotten password resetting - new password setting</h4>
            <p>Please give a new password to your account and its repeat as confirmation!.</p>
            <form>
                <InputUnit labeling='New Password' 
                    type='password' invalidInputs={errors}
                    name='password' value={values.password} 
                    funcChange={onChange}
                />
                <InputUnit labeling='Confirm password' 
                    type='password' invalidInputs={errors}
                    name='passwordconf' value={values.passwordConfirm} 
                    funcChange={onChange}
                />
                <input type='submit' value='Change password' onClick={onSubmit}/>
                { loading && <p>Under process...</p> }
                { errors.message.length > 0 && errors.message.map((errMsg, index)=>{
                    return <ErrorUnit key={index} text={errMsg} />
                }) }
            </form>
        </div>
    )
}

export default PwdResetStage2

const RESET_PWD1 = gql`
    mutation resetPasswordStep3(
        $password: String!
        $passwordconf: String!
    ){
        resetPasswordStep3(
            newpassword: $password,
            newconf: $passwordconf
        ){
            resultText, id, email, username    
        }
    }
`