import gql from 'graphql-tag'
import { useMutation } from '@apollo/client';

import { 
    useFormUsageActions, useFormErrorActions, useLogRestriction
} from '../../utils/myHookFunctions'
import { localFormDataRevisions } from '../../utils/dataRevision'

import InputUnit from '../generalForm/InputUnit'
import ErrorUnit from '../generalForm/ErrorUnit'
function PwdResetStage1(){
    useLogRestriction('LOGOUT_NEEDED', '/') //at logged in state redirection

    const userFormValidationCrit = ['email']
    const { errors, resetErrors, setValidationErrors, setGeneralErrors 
        } = useFormErrorActions()

    const { values, onSubmit, onChange, clearFields 
        } = useFormUsageActions({ email: '' }, userResetEmail)

    const [ resetStp1Process, { loading } ] = useMutation(RESET_PWD1, {
        update(_, { data: { resetPasswordStep1: accountProcess }} ){
            clearFields()
            resetErrors()
        },
        onError(err){
            console.log(err)
            setGeneralErrors(err.message)
        },
        variables: values
    })

    function userResetEmail(){
        resetErrors()
        const clientErrors = localFormDataRevisions(values, userFormValidationCrit)
        if(clientErrors.field.length === 0){
            resetStp1Process()
        }else{
            setValidationErrors(clientErrors)
        }
    }


    return (
        <div>
            <h4>Forgotten password resetting - sending authenticator email</h4>
            <p>For resetting forgotten password, please give your email which you have been registered to the site!</p>
            <p>You will get an email with a link URL, with which you can continue the forgotten password resetting process.</p>
            <form>
                <InputUnit labeling='Email' 
                    type='email' invalidInputs={errors}
                    name='email' value={values.email} funcChange={onChange}
                />
                <input type='submit' value='Continue' onClick={onSubmit}/>
                { loading && <p>Under process...</p> }
                { errors.message.length > 0 && errors.message.map((errMsg, index)=>{
                    return <ErrorUnit key={index} text={errMsg} />
                }) }
            </form>
        </div>
    )
}

export default PwdResetStage1

const RESET_PWD1 = gql`
    mutation resetPasswordStep1(
        $email: String!
    ){
        resetPasswordStep1(
            email: $email
        ){
            resultText, id, email, username    
        }
    }
`