
/** __For expiration time resolving__ */
export const convertExpiration_StrToInt = (tokenExpireInStr)=>{
    try{
        return Number.parseInt(tokenExpireInStr) 
    }catch(err){
        return 0    //this case, makes expiration true
    }
}

/** __For exiration time persistence__ */
export const convertExpiration_IntSecToIntMiliSec = (tokenExpireInSecInt)=>{
    return Date.now() + (tokenExpireInSecInt * 1000)
}


