import React from 'react';
import App from './App';
import ApolloClient  from 'apollo-client';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { ApolloLink, from } from 'apollo-link'
import { createHttpLink } from 'apollo-link-http';
import { ApolloProvider } from '@apollo/react-hooks';
//import { setContext } from 'apollo-link-context';

import { 
   getAutorizationToken, getRefreshToken
   } from './utils/localStorageProc'

const httpLink = createHttpLink({
   //uri: 'https://infinite-ridge-04284.herokuapp.com/graphql'
   uri: 'http://localhost:4040/graphql'
});

const headerSetting = new ApolloLink((operation, forward)=>{
   
   if(operation.getContext('refreshing')){
      operation.setContext(({ headers = {} }) => ({
         headers: {
           ...headers,
           refreshing: getRefreshToken() || null,
         }
      }));
   }
   operation.setContext(({ headers = {} }) => ({
      headers: {
         ...headers,
         authorization: 'Bearer ' + (getAutorizationToken() || ''),
      }
   }));


   return forward(operation);
})

const headerAbsorb = new ApolloLink((operation, forward)=>{
   return forward(operation).map(data=>{


      return data
   });
})

/*
const authLink = setContext(() => {
   return {
      headers: {
         Authorization: `Bearer ${getAutorizationToken()}`
      }
   };
});

const refreshLink = setContext((request) => {
   delete request.headers.Authorization
   return {
      headers: {
         Refreshing: `${getRefreshToken()}`
      }
   };
});
*/

const client = new ApolloClient({
   //link: authLink.concat(httpLink),
   link: from([headerSetting, headerAbsorb, httpLink]),
   cache: new InMemoryCache()
});
  
export default (
   <ApolloProvider client={client}>
      <App />
   </ApolloProvider>
);