import React from "react";
import { useContext }  from 'react'
import { Route, Switch } from 'react-router-dom'

import { AuthContext } from '../utils/authorization'

import LoginPage from '../pages/LoginPage';
import RegisterPage from '../pages/RegisterPage';
import PwdResetPage from '../pages/PwdResetPage';
import WellcomeContent from '../components/outContent/WellcomeContent'

import LogoutProcess from '../components/inContent/LogoutProcess'
import ReviviewContent from "../components/inContent/ReviewContent";
import ChatsList from '../components/inContent/ChatsList'
import MyAccount from '../components/inContent/MyAccount'
import FriendList from '../components/inContent/FriendsList'
import PostsList from '../components/inContent/PostsList'
import ServerErrorPage from "./ServerErrorPage";

function HomePage(){
   const userContext = useContext(AuthContext)
   return (
      <>
         { userContext.user ? <Switch>
            <Route path='/logout' component={LogoutProcess} />
            <Route path={`/account`} component={MyAccount} />
            <Route path={`/friends`} component={FriendList}/>
            <Route path={`/posts`} component={PostsList} />
            <Route path={`/chats`} component={ChatsList} /> 
            <Route path='/' component={ReviviewContent} />
         </Switch> : <Switch>
            <Route exact path='/login' component={ LoginPage } />
            <Route exact path='/registration' component={ RegisterPage } />
            <Route path='/passwordresetting' component={ PwdResetPage } />
            <Route path='/error' component={ServerErrorPage} />
            <Route path='/' component={ WellcomeContent } />
         </Switch>}
      </>
   )
}

export default HomePage