import { useContext } from 'react'
import { Link, Redirect } from 'react-router-dom'
import { useMutation } from '@apollo/react-hooks'
import gql from 'graphql-tag';

import { useFormUsageActions, useFormErrorActions, useLogRestriction 
} from '../utils/myHookFunctions'
import { localFormDataRevisions } from '../utils/dataRevision'
import { AuthContext } from '../utils/authorization';
import InputUnit from '../components/generalForm/InputUnit'
import ErrorUnit from '../components/generalForm/ErrorUnit'

function LoginPage(){
    useLogRestriction('LOGOUT_NEEDED', '/') //at logged in state redirection

    const userFormValidationCrit = ['email', 'password']

    const { errors, resetErrors, setValidationErrors, setGeneralErrors } = useFormErrorActions()

    const { values, onSubmit, onChange } = useFormUsageActions({
        email: '',
        password: ''
    }, userLogin)
    const userContext = useContext(AuthContext)

    const [ loginProcess, { loading } ] = useMutation(LOGIN_USER, {
        update(_, { data: { login: userLogDatas }} ){
            userContext.login(userLogDatas)
            resetErrors()
        },
        onError(err){
            console.log(err)
            setGeneralErrors(err.message)
        },
        variables: values
    })

    function userLogin(){
        resetErrors()
        const clientErrors = localFormDataRevisions(values, userFormValidationCrit)
        if(clientErrors.field.length === 0){
            loginProcess()
        }else{
            setValidationErrors(clientErrors)
        }
    }

    return (
        <div>
            <h3>Login page</h3>
            { userContext.user? <Redirect to='/' /> : <></>}
            <form>
                <InputUnit labeling='Email' 
                    type='text' invalidInputs={errors}
                    name='email' value={values.email} funcChange={onChange}
                />
                <InputUnit labeling='Password' 
                    type='password' invalidInputs={errors}
                    name='password' value={values.password} funcChange={onChange}
                />
                <input type='submit' value='Login' onClick={onSubmit}/>
                { loading && <p>Under process...</p> }
                { errors.message.length > 0 && errors.message.map((errMsg, index)=>{
                    return <ErrorUnit key={index} text={errMsg} />
                }) }
            </form>
            <p>
                <Link to='/passwordresetting/email'>
                    Did you forget the password?
                </Link>
            </p>
        </div>
    )
}

const LOGIN_USER = gql`
    mutation login(
        $email: String!
        $password: String!
    ){
        login(
            email: $email
            password: $password
        ){
            id, email, username, token, tokenExpire, refreshToken, registeredAt, lastLoggedAt
        }
    }
`

export default LoginPage