import React from "react";
import { Switch, Route, Redirect, useRouteMatch } from 'react-router-dom'

import PwdResetStage1 from '../components/outContent/PwdResetStage1'
import PwdResetStage2 from '../components/outContent/PwdResetStage2'
import PwdResetStage3 from '../components/outContent/PwdResetStage3'

import { usePathGetter } from '../utils/myHookFunctions'

function PwdResetPage(){
    const { path, url } = useRouteMatch()
    return (
        <div>
            <h3>Forgotten password resetting page</h3>
            <Switch>
                <Route path={ `${path}/email` }>
                    <PwdResetStage1 />
                </Route>
                <Route path={ `${path}/result` }>
                    <PwdResetStage3 />
                </Route>

                {/* for server GET response only */}
                <Route path={ `${path}/:token` }>
                    <PwdResetStage2 />
                </Route>
                {/* for no match case */}
                <Redirect from={`${url}`} to='/' />
            </Switch>
        </div>

    )
}

export default PwdResetPage
