import { 
    convertExpiration_StrToInt, 
    convertExpiration_IntSecToIntMiliSec } from './dataConverting'
import { reviseAutTokenExpiry } from './dataRevision'

/** __Helper local storage direct processes__ */
function persistLocalCredentials( labels, values ){
    for(let i = 0; i < labels.length; i++){
        localStorage.setItem(labels[i], values[i])
    }
}

function removeLocalCredentials(labels){
    for(let i = 0; i < labels.length; i++){
        localStorage.removeItem(labels[i])
    }
}


/** __Helper local storage getter processes__ */
export function didUserDatasPersistLocally(){
    const res = {
        refreshToken: localStorage.getItem('refreshToken'),
        token: localStorage.getItem('authToken'),
        expireDate: localStorage.getItem('expireDate')
    } 
    if(Object.values(res).includes(null)){
        return false;
    }
    return res
}

export function getAutorizationToken(){
    return localStorage.getItem('authToken')
}

export function getRefreshToken(){
    return localStorage.getItem('refreshToken')
}

export function didAuthTokenExpired(){
    const expirStr = localStorage.getItem('expireDate')
    const expireDateInMs = convertExpiration_StrToInt(expirStr)
    return reviseAutTokenExpiry(expireDateInMs)
}



/** __Helper local storage mutation processes__ */
export function persistRefresedCredentials(authResObj){
    persistLocalCredentials(
        ['authToken', 'expireDate' ], 
        [ 
            authResObj.newToken, 
            convertExpiration_IntSecToIntMiliSec(authResObj.tokenExpire)
        ]
    )
}

export function persistAllEssentialCredentials(userData){
    persistLocalCredentials(
        ['authToken', 'refreshToken', 'expireDate'],
        [ 
            userData.token, userData.refreshToken, 
            convertExpiration_IntSecToIntMiliSec(userData.tokenExpire)
        ]
    )
}

export function clearAllLocalCredentials(){
    removeLocalCredentials(
        ['authToken', 'refreshToken', 'expireDate']
    )
}

